  title: "Visitamos Amazon Go: así es la experiencia de compra en una tienda semi automatizada sin cajeros",
  story: "Entramos a la tienda, escogemos lo que queremos y salimos sin tener que pasar por una caja o por un ser humano que nos cobre. Esta es la premisa detrás de <em>Amazon Go</em>, la primera tienda semi automatizada del gigante de las ventas en línea, la cual causó furor cuando fue lanzada hace un año en una especie de beta cerrada para empleados de la compañía. Pues la tienda ya está abierta para todo el mundo y fuimos a Seattle para visitarla para conocer cómo es la experiencia y confirmar, o desmentir, todo lo que se dice alrededor de ella.<br><br>Entones, ¿es cierto que no hay empleados? ¿cuán grande es la tienda? ¿Es fiable o es posible engañar al sistema? ¿Si no hay cajas, cómo pagamos? Esta y otras preguntas me surgían mientras estaba en el avión rumbo a Seattle, y es que a la distancia todo suena como un cambio radical en la forma que tenemos de comprar en todo el mundo, donde entramos, escogemos y pagamos en una caja. Pues aquí se elimina ese último paso, y a pesar de que podría sonar sencillo, detrás hay un complejo sistema que lo hace posible.<br><br>Llegué a la tienda, y antes de entrar me encontré con varios letreros en una especie de zona de espera que me pedían descargar la aplicación móvil. La app es nuestra llave de entrada a Amazon Go, además de que sirve para llevar un registro de nuestras compras y configurar la forma de pago.<br><br><img class='ui centered fluid image' src='"+ data01 +"'><br><br>No es necesario llevar efectivo a la tienda, no lo necesitamos, todo se hace a través de nuestra cuenta de Amazon. Antes de llegar yo ya había descargado la aplicación y el proceso de configuración fue extremadamente sencillo, me llevó cerca de dos minutos tener todo listo.<br><br>La aplicación funcionó con mis datos de Amazon México y después de poner mi usuario y contraseña me aparecieron las tarjetas que tengo registradas en Amazon. El siguiente paso fue elegir la tarjeta a donde se harán los cargos dentro de la Amazon Go. Hasta aquí todo controlado, ahora lo siguiente era entrar a la tienda.",
