  title: "Hackear WhatsApp: los peligros de las aplicaciones que prometen espiar conversaciones ",
  story: "Por una extraña razón muchísimos usuarios quieren saber cómo hackear WhatsApp y poder espiar a otras personas. ¿Qué necesidad hay de meterse en la vida privada de los demás? No nos vamos a meter en eso, pero conviene recordar que espiar conversaciones es delito. Dicho esto, todavía hay muchas personas que descargan aplicaciones que prometen acceder al historial de conversaciones de WhatsApp.<br><br>En Google Play encontramos un largo historial de aplicaciones milagrosas que prometen poder espiar el WhatsApp de otros. Y en internet también vemos decenas de tutoriales a cada cuál más preciso para poder infiltrarse en los chats de otra persona. Lo cierto es que muchas de ellas son un timo absoluto.<br><br>Hemos descargado algunas de ellas para ver si cumplen lo que prometen pero ya adelantamos que lo que hemos encontrado decepcionará a todo aquel que busque realmente espiar el WhatsApp de otros. ",
