  title: "Bitcoin es un absoluto desastre energético (y medioambiental): cuánta energía consume realmente",
  story: "Bitcoin es el futuro para muchos criptoentusiastas, o más bien deberíamos decir que la criptoeconomía es el futuro: un futuro que están seguros de que ha llegado a nuestro mundo para quedarse de una manera u otra. Pero ello no implica automáticamente que todas las criptomonedas y tokens vayan a formar parte de ese futuro, ni mucho menos que todas ellas sólo posean incontables e idílicas ventajas y ninguna desventaja.<br><br>Como en todo progreso o avance de cualquier índole, también hay evidentemente algunas grandes desventajas en el caso concreto de Bitcoin. Algunas de esas desventajas ya las vemos a día de hoy, como su limitación de operaciones por segundo que diversos forks están tratando de solventar con tanta polémica interna, y otras sólo podremos verlas conforme vaya llegando un futuro siempre impredecible.<br><br><img src='"+data02+"' class='ui centered fluid image'><br><br>Esta sencilla pregunta puede parecer que tiene una fácil respuesta, y no. Nada más lejos de la realidad. Lo cierto es que estimar el consumo total de energía que Bitcoin está absorbiendo es una difícil tarea. Una tarea en la que ya se han puesto manos a la obra algunos criptoentusiastas, conscientes de que el tema energético puede suponer el talón de Aquiles de todo el ecosistema Bitcoin.<br><br>Una de las premisas fundamentales es asumir (sorprendentemente) que, de media, los mineros de Bitcoin gastan el 60% de sus ingresos en costes operacionales, además de que, de esos costes, cada 5 céntimos de gasto implican un consumo de 1kWh de electricidad. ¿Cuál es la base empírica para poder afirmar que estas asunciones son correctas? El quid de la cuestión es que las justificaciones rigurosas que avalan esta asunción brillan por su ausencia, por lo que estas cifras no deben ser automáticamente adoptadas como axioma.",
