  title: "Salon da el paso: si bloqueas nuestros anuncios usaremos tu ordenador para minar criptomonedas",
  story: "En Salon no cuentan con muros de pago que planteen esa alternativa del modelo de suscripción que otros han puesto en marcha con mayor o menor éxito: sus ingresos provienen de la publicidad que muestran a los lectores cuando estos visitan sus páginas.<br><br><img class='ui centered fluid image' src='"+data03+"'<br><br>La aparición de los distintos sistemas de adblocking que primero aparecieron como complementos de los navegadores y que desde hace tiempo incluso van integrados en ellos hace que obviamente los ingresos publicitarios de estos medios puedan verse reducidos de forma notable.<br><br>Eso ha hecho que en Salon actúen para contrarrestar esa pérdida de beneficios, y en una sección FAQ explican qué pasa con su nuevo sistema de monetización. En ese sistema uno puede acceder al medio de forma normal con los anuncios habituales. Si tiene un navegador que bloquea esa publicidad, el sitio web lo detecta y ofrece una ventana informativa: o sigue viendo los anuncios y desactiva el bloqueo, o elige la modalidad \"Supress Ads\".<br><br>En esta modalidad \"bloqueas los anuncios y permites que Salon use tu potencia de computación no utilizada\". O lo que es lo mismo: aprovechan tu CPU y GPU para minar criptodivisas mientras estás leyendo sus artículos.<br><br>Ese minado de criptodivisas en páginas web se ha ido popularizando en los últimos meses. El experimento de The Pirate Bay acabó extendiéndose a otros tipos de sitios web —es una práctica habitual entre los sitios web de contenidos pornográficos—, y de hecho ultimamente se está convirtiendo en un tipo de ciberataque cada vez más extendido.",
