  title: "Cómo probar AMP Stories, las nuevas historias de Google",
  story: "Las historias que han popularizado Instagram, WhatsApp y Facebook están aquí para quedarse. Se han convertido en un nuevo formato visual para contar información, que sobretodo están exprimiendo los medios de comunicación para contar noticias.<br><br>Y como a los medios de comunicación les gusta esa forma de contar las noticias Google no se ha quedado quieta y ha presentado su propio formato abierto para mostrar historias: las nuevas AMP Stories.<br><br><img class='ui centered medium image' src='"+data04+"'><br><br>El Proyecto AMP quiere ofrecer a los editores de contenido un nuevo formato para que puedan ofrecer noticias e información como historias visualmente ricas en dispositivos móviles.<br><br>Con las nuevas AMP Stories los medios podrán captar la atención de sus lectores con pequeñas historias que resuman las noticias acompañadas de imágenes, videos, música y gráficos.<br><br>El formato AMP Stories se basa en la infraestructura técnica de AMP para proporcionar una experiencia rápida y los editores pueden añadirlos a su sitio web como un componente más en HTML.<br><br>Google está probando las nuevas AMP Stories con un grupo de editores para crear este nuevo formato. Este formato ofrece a los profesionales plantillas de diseño preestablecidas pero flexibles para la creación de las historias.<br><br><img class='ui fluid centered image' src='"+data05+"'><br><br>Las nuevas AMP Stories están integradas en la Búsqueda de Google. Cuando busques algún periódico, blog o medio podrás ver entre sus resultados una nueva sección llamada Visual Stories que mostrará las historias relacionadas con esa noticia o con ese medio.<br><br>Las AMP Stories están todavía en su fase de pruebas pero desde ya podemos probarla entrando en <a href='https://www.google.com/webhp?hl=en&gl=US&gws_rd=cr&esrch=AmpStories::Preview,AmpStoriesDesktop::Promo'>g.co/ampstories</a>. Para encontrar las historias tenemos que buscar el nombre de alguno de los editores involucrados en el proyecto: CNN, Conde Nast, Hearst, Mashable, Meredith, Mic, Vox Media y The Washington Post.",
