  title: "'PlayerUnknown’s Battlegrounds' llega a Android: probamos el juego de batallas multijugador que está arrasando",
  story: "Un avión sobrevuela una isla deshabitada dejando a su paso decenas de jugadores planeando en sus paracaídas. Conforme tocan el suelo todos corren a abastecerse de armas y equipamiento de combate. Sólo puede quedar uno. ¿Te suena? No es 'Battle Royale' ni 'Los Juegos del Hambre', sino 'PlayerUnknown’s Battlegrounds', también conocido por sus siglas PUBG.<br><br>El juego de batallas online está arrasando en su versión para PC y Xbox y ahora se prepara para conquistar el universo móvil. El gigante chino Tencent es el encargado de traernos esta aventura multijugador a nuestros móviles. El juego, llamado 'PlayerUnknown’s Battlegrounds: Exhilarating Battlefield' ya está disponible en China y lo hemos descargado y probado. Además, viene acompañado de una versión llamada 'PlayerUnknown’s Battlegrounds: Army Attack'.<br><br>Antes de empezar, insistimos en que Tencent ha lanzado PUBG para móviles solamente en China, lo que significa, no sólo que el juego está en perfecto mandarín, sino que para poder acceder es necesario iniciar sesión con WeChat o QQ. La opción más adecuada es hacer una cuenta en WeChat ya que QQ nos pide un número de teléfono de China.Primero vamos con 'Exhilarating Battlefield', la versión de PUBG que promete más fidelidad con el juego original, y la verdad es que lo consigue. Tras iniciar la batalla con algunas dificultades por el tema del idioma, nuestro avatar salta del avión y empieza la guerra. Por supuesto, también hay que ir cambiando de posición para evitar quedarnos fuera del área de juego, exactamente igual que en la versión de PC y consola.<br><br>Los controles están bastante bien optimizados para paneles táctiles y pese a que contamos con unos cuantos botones en pantalla estos no resultan demasiado intrusivos.. Para movernos tenemos un joystick en la parte izquierda, mientras que la cámara se mueve deslizando con el otro pulgar sobre la pantalla, igual que haríamos con un doble joystick pero con gestos táctiles. A la derecha de la pantalla tenemos los botones para agacharnos, reptar, saltar y atacar, ya sea con los puños o con el arma que tengamos equipada.<br><br>Como decíamos, Tencent viene fuerte y ha lanzado no una, sino dos versiones del popular juego de batallas. En esta segunda edición apodada 'Army Attack' también tenemos que lanzarnos en paracaídas en una isla donde nos enfrentaremos a un centenar de jugadores hasta que sólo quede uno. A priori todo igual que en el original.<br><br>Una de las diferencias con el juego original son los vehículos, entre los que podemos encontrar vehículos marítimos y aéreos, aunque si queréis que os diga la verdad, en casi media hora jugando no he visto ninguno. Habrá que explorar más para encontrarlos.",
